
import { Options, Vue, Prop } from "vue-property-decorator";

@Options({
  name: "ui-banner",
})
export default class UiBannerComponent extends Vue {
  @Prop({ default: "" })
  subtitle: string;
  @Prop({ default: "" })
  image: string;
  @Prop({ default: "" })
  title: string;
  @Prop({ default: "" })
  description: string;

  get cardStyles() {
    return [{ backgroundImage: `url(${this.image})` }];
  }
}
