
import { Options, Vue } from "vue-property-decorator";

import { NewsId_1 } from "./vars/News";

@Options({
  name: "one-news-page",
})
export default class OneNewsPageComponent extends Vue {
  newsId: string | string[] = "";

  created() {
    this.initNewsNumber();
    window.scroll(0, 0);
  }

  initNewsNumber() {
    this.newsId = this.$route.params.id;
  }

  get news() {
    switch (this.newsId) {
      case "1":
        return NewsId_1;
    }
    return "";
  }
}
