
import { Options, Vue, Prop } from "vue-property-decorator";

@Options({
  name: "ui-link",
})
export default class UiLinkComponent extends Vue {
  @Prop({ default: "" })
  path: string;
  @Prop({ default: "" })
  name: string;
  @Prop({ default: "" })
  href: string;
  @Prop({ default: "blank" })
  target: string;
  @Prop({ default: "" })
  image: string;
  @Prop({ default: false })
  isDownload: boolean;

  goToPage() {
    if (this.name) this.$router.push({ name: this.name });
    else if (this.path) this.$router.push({ path: this.path });
  }
}
